<template>
	<Tabs value="applyout" class="tab">
		<Tab-pane label="强行出库申请" name="applyout" class="tab-item">
			<div class="order-cell">
				<div class="cell-title">基本信息</div>
				<div class="cell-body">
					<div class="cell-item">
						<span class="order-item-title">入库申请编号：</span><span>{{info.gMId | empty}}</span>
					</div>
					<div class="cell-item">
						<span class="order-item-title">贷款编号：</span><span>{{info.loanId | empty}}</span>
					</div>
				</div>
			</div>
			<div class="order-cell" v-for="item in info.mhList">
				<div class="cell-title">抵押品信息 -- （房）-- {{item.mHId}}</div>
				<div class="cell-body">
					<div class="cell-item">
						<span class="order-item-title">押品编号：</span><span>{{item.guaranteeNo | empty}}</span>
					</div>
					<div class="cell-item">
						<span class="order-item-title">评估金额：</span><span>{{item.mhouseValue | fmoney | empty}}</span>
					</div>
					<div class="cell-item">
						<span class="order-item-title">产权所有人：</span><span>{{item.propertyOwner | empty}}</span>
					</div>
					<div class="cell-item">
						<span class="order-item-title">产权地址：</span><span>{{item.gAddress | empty}}</span>
					</div>
					<div class="cell-item">
						<span class="order-item-title">房屋区域：</span><span>{{item.houseArea | empty}}</span>
					</div>
					<div class="cell-item">
						<span class="order-item-title">建筑面积：</span><span>{{item.acreage | empty}}m²</span>
					</div>
					<div class="cell-item">
						<span class="order-item-title">权证类型：</span><span>{{item.warrantType | empty}}</span>
					</div>
					<div class="cell-item">
						<span class="order-item-title">权证号码：</span><span>{{item.propertyRightNumber | empty}}</span>
					</div>
				</div>
			</div>
			<div class="apply-out-form">
				<div class="loan-title">出库信息</div>
				<div class="cell-body">
					<div class="cell-item">
						<span class="order-item-title">出库原因：</span><span>{{info.outReason}}</span>
					</div>
				</div>
				<div class="form-box">
					<Form ref="formInline" inline label-position="left">
						<div class="out-info">
							<span class="form-item-title">出库说明：</span>
							<Input class="areatext-box" v-model="outRemark" type="textarea" :maxlength="100" :rows="4"
								   placeholder="强行出库原因（100字以内）..."></Input>
						</div>
						<up-load v-if="info.scratchFileList"
								 :labelWidth="70"
								 :imgList="info.scratchFileList"
								 :actionUrl="actionUrl"
								 :upData="upData"
								 @upLoad="handleUpLoad"></up-load>
						<Form-item class="sub-item">
							<i-button class="sub" type="primary" @click="submit">确定</i-button>
							<i-button class="cancel" type="ghost" @click="pageBack">取消</i-button>
						</Form-item>
					</Form>
				</div>
			</div>
		</Tab-pane>
		<i-button type="text" slot="extra" class="extra-btn" icon="ios-undo-outline" @click="pageBack">返回</i-button>
	</Tabs>
</template>
<script>
	import upLoad from '@/components/upload.vue'
	export default {
		data () {
			return {
				upData:{
					aBaseName:'',
					loanId:'',
					serialNo:'',
					eProduct:'YPGL'
				},
				actionUrl:'/imagesDo/uploadImages',
				upList:[],
				outRemark: '',
				info: {},
				baseInfo: {}
			}
		},
		components: {
			upLoad
		},
		created: function () {
			this.baseInfo = JSON.parse(sessionStorage.getItem('baseInfo'));
			this.loadInfo();
		},
		methods: {
			loadInfo: function () {
				let _this = this;
				let params = _this.$route.params;
				if (params.loanId) {
					_this.$http.post('Storage/searchInOutProduct', {
						loanId: params.loanId,
						serialNo:params.serialNo
					})
						.then(function (response) {
							let res = response.data;
							if (res.code == '1') {
								_this.$Message.success({
									content: res.msg
								});
								_this.info = res.data;
								_this.defaultList=JSON.parse(res.data.loanImage);
								_this.upData.loanId=res.data.loanId;
								_this.upData.serialNo=res.data.serialNo;
								_this.upData.aBaseName=res.data.fileRPath.storage;
							} else {
								_this.$router.go(-1);
								_this.$Message.error({
									content: res.msg,
									duration: 3
								});
							}
						})
						.catch(function (error) {
							//处理错误
							_this.$Message.error({
								content: error,
								duration: 3
							});
						});
				} else {
					_this.$Message.error({
						content: '贷款编号错误！',
						duration: 3
					});
					_this.pageBack();
				}
			},
			handleUpLoad:function(list){
				this.upList=list;
			},
			submit: function () {
				var _this = this;
				let loanId = _this.$route.params.loanId;
				if (loanId) {//贷款编号是否为空
					_this.$http.post('Storage/alterProductAccessory', {
						loanId: loanId,
						status: 7,
						outRemark: _this.outRemark,
						loanVoucher: JSON.stringify(_this.upList)
					})
						.then(function (response) {
							let res = response.data;
							if (res.code == 1) {
								_this.$Message.success(res.msg);
								_this.pageBack();
							} else {
								_this.$Message.error(res.msg);
							}
						})
						.catch(function (error) {
							_this.$Message.error({
								content: error
							});
						})
				} else {
					_this.$Message.error({
						content: '贷款编号错误！',
						onClose: function () {
							_this.pageBack();
						}
					});
					return false
				}
			},
			pageBack: function () {//返回，取消
				var _this = this;
				let tab = _this.$route.params.tab;
				if (tab) {
					_this.$router.push({name: 'pawnout', params: {tab: tab}})
				} else {
					_this.$router.go(-1);
				}
			}
		}
	}
</script>
<style>
	.tab {
		padding-top: 15px;
	}

	.ivu-tabs-tab {
		font-size: 16px;
	}

	.layout-content .extra-btn {
		color: #20abff;
		font-size: 14px;
	}

	.order-title {
		text-align: right;
		color: #343434;
		font-size: 14px;
	}

	.order-cell {
		padding-bottom: 5px;
		border-bottom: 1px solid #e5e5e5;
		margin-bottom: 30px;
	}

	.cell-title {
		height: 14px;
		border-left: 3px solid #ff8226;
		color: #ff8226;
		font-size: 14px;
		line-height: 14px;
		padding-left: 10px;
		margin-bottom: 30px;
	}

	.cell-body {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: flex-start;
		align-items: center;
	}

	.cell-item {
		min-width: 300px;
		padding-bottom: 15px;
		padding-right: 20px;
		color: #333333;
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: center;
		font-size: 14px;
	}

	.order-item-title {
		width: 100px;
		height: 22px;
		line-height: 22px;
		color: #7f7f7f;
		overflow: hidden;
	}

	.apply-out-form .loan-title {
		width: 110px;
		height: 34px;
		line-height: 34px;
		background-color: #20abff;
		text-align: center;
		color: #fff;
		font-size: 14px;
		margin-bottom: 30px;
	}

	.form-item {
		width: 360px;
		padding-bottom: 6px;
	}

	.form-input {
		width: 220px;
	}

	.out-info {
		display: flex;
		justify-content: flex-start;
		align-items: center;
		flex-direction: row;
		padding-bottom: 15px;
	}

	.areatext-box {
		width: 640px;
	}

	.upload-box {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: flex-start;
		align-items: center;
	}

	.form-item-title {
		width: 80px;
		color: #323d4d;
	}

	.demo-upload-list {
		display: inline-block;
		width: 60px;
		height: 60px;
		text-align: center;
		line-height: 60px;
		border: 1px solid transparent;
		border-radius: 4px;
		overflow: hidden;
		background: #fff;
		position: relative;
		box-shadow: 0 1px 1px rgba(0, 0, 0, .2);
		margin-left: 10px;
	}

	.demo-upload-list img {
		width: 100%;
		height: 100%;
	}

	.demo-upload-list-cover {
		display: none;
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		background: rgba(0, 0, 0, .6);
	}

	.demo-upload-list:hover .demo-upload-list-cover {
		display: block;
	}

	.demo-upload-list-cover i {
		color: #fff;
		font-size: 20px;
		cursor: pointer;
		margin: 0 2px;
	}

	.sub-item {
		width: 500px;
		padding-top: 100px;
		padding-bottom: 50px;
	}

	.sub {
		width: 200px;
		height: 50px;
		margin-right: 30px;
		background-color: #20abff;
		border-color: #20abff;
		font-size: 18px;
		letter-spacing: 5px;
	}

	.cancel {
		width: 200px;
		height: 50px;
		border-color: #20abff;
		color: #20abff;
		font-size: 18px;
		letter-spacing: 5px;
	}
</style>




